.centered-text {
  position: fixed;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -0%);
  font-size: 1.2vw; 
  text-align: center;
}

.big-text {
  font-family: 'Papyrus';
  color: red;
}

.subheading-one{

  font-family: 'Fira Sans';
  color: green;

}

.subheading-two{

  font-family: 'Courier';
  color: purple;
  margin-bottom: '100vw';

}


